<script>
export default {
  name: "frontPageComponent",
  data() {
    return {
      loginType: {},
      typeData: {},
      projectData: {},
      vprojectData: {},
      dailyProjectData: {},
      vdailyProjectData: {},
      ticData: {},
      insData: {},
      ungencyData: {},
      vendorMapping: [],
    };
  },
  methods: {
    async loadData() {
      var vm = this;
      //await vm.switchLoginType();
      vm.$store.dispatch("GetResultsList").then((res) => {
        //判斷廠商身份
        //vm.loginType = data0;
        const data0 = res.data.data.result0;
        vm.typeData = data0;
        //console.log(res.data.data.result0);
        if (res.data.data.result0.Value == true) {
          vm.loginType = "vendor";
        } else {
          vm.loginType = "employee";
        }
        const data = res.data.data.result1; //連絡窗口
        vm.vendorMapping = data;
        const data2 = res.data.data.result2; //廠商工程單
        vm.vprojectData = data2;
        const data3 = res.data.data.result3; //廠商施工單
        vm.vdailyProjectData = data3;
        const data4 = res.data.data.result4; //廠商罰單
        vm.ticData = data4;
        const data5 = res.data.data.result5; //廠商保險快到期
        vm.insData = data5;
        const data6 = res.data.data.result6; //員工工程單
        vm.projectData = data6;
        const data7 = res.data.data.result7; //員工施工單
        vm.dailyProjectData = data7;
        const data8 = res.data.data.result8; //員工搶修單
        vm.ungencyData = data8;
      });
      //vm.$store.dispatch("getContactList").then((res) => {
      //  const data = res.data.data;
      //  vm.projectData = data;
      //});
      //vm.$store.dispatch("getProjectByINX").then((res) => {
      //  const data = res.data.data;
      //  vm.dailyProjectData = data;
      //});
    },
    async switchLoginType() {
      var vm = this;
      vm.$store.dispatch("checkVendorRole").then((res) => {
        //const data = res.data.data;
        console.log(res);
        if (res == true) {
          vm.loginType = "vendor";
        } else {
          vm.loginType = "employee";
        }
        //if (vm.loginType == "vendor") vm.loginType = "vendor";
        // else vm.loginType = "employee";
      });
    },
  },
  mounted() {
    this.loadData();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./frontPage.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
